body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


li {
  list-style-type: none;
}
ul {
  margin-left: 0;
  padding-left: 0;
}


input {
  border:none;
  background:none;
  outline:none;
  padding:0;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

button:hover, button:active, button:focus {
  outline: none;
}


::-webkit-scrollbar { width: 5px; height: 3px;}
::-webkit-scrollbar-button {  background-color: #666; }
::-webkit-scrollbar-track {  background-color: transparent;}
::-webkit-scrollbar-track-piece { background-color: transparent;}
::-webkit-scrollbar-thumb { height: 30px; background-color: #666; border-radius: 4px;}
::-webkit-scrollbar-corner { background-color: #999;}
::-webkit-resizer { background-color: #666;}
.welcome {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  padding: 40px 80px;
  border-radius: 20px;
}
.welcome-fixed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
}
.welcome input,
.welcome button {
  border: 1px solid black;
  padding: 5px 10px;
  border-radius: 4px;
  margin-top: 5px;
}
.welcome button {
  margin-top: 25px;
}
.welcome input.error::-webkit-input-placeholder {
  color: red;
}
.welcome input.error::-ms-input-placeholder {
  color: red;
}
.welcome input.error::placeholder {
  color: red;
}
/*# sourceMappingURL=index.css.map */
.msg {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-items: flex-start;
  background: transparent;
  margin-bottom: 7px;
}
.msg-header {
  color: #2a2b37;
  background: transparent;
}
.msg-mainText {
  word-wrap: break-word;
  background: #2a2b37;
  padding: 5px 15px;
  border-radius: 15px;
  color: white;
  max-width: 60%;
}
.msg.my-msg {
  align-items: flex-end;
}
/*# sourceMappingURL=index.css.map */
.Room-chat {
  border: 4px solid #2a2b37;
  height: 80vh;
  position: relative;
}
.Room-chat-messages {
  height: calc(100% - 60px);
  overflow-y: auto;
  padding: 15px 10px;
}
.Room-chat-input {
  border-top: 1px solid #2a2b37;
  height: 60px;
}
.Room-chat-input input,
.Room-chat-input button {
  height: calc(100% + 1px);
  position: relative;
  top: -1px;
}
.Room-chat-input input {
  width: 85%;
  padding: 0 15px;
  top: -3px;
}
.Room-chat-input button {
  width: 15%;
  border-left: 1px solid #2a2b37;
  font-size: 24px;
}
/*# sourceMappingURL=index.css.map */
.onlineList {
  position: fixed;
  top: 50%;
  left: 50%;
  background: cadetblue;
  color: #2a2b37;
  padding: 40px 80px;
  z-index: 1;
  border-radius: 4px;
  -webkit-animation: fadeInDown 0.5s forwards;
          animation: fadeInDown 0.5s forwards;
  box-shadow: 1px 1px 25px 1px #2a2b37;
}
.onlineList header {
  font-size: 24px;
  font-weight: 700;
  text-align: center;
}
.onlineList main {
  height: 400px;
  min-width: 250px;
  margin: 15px 0;
  overflow-y: auto;
}
.onlineList main ul li {
  margin-bottom: 5px;
  font-size: 18px;
}
.onlineList button {
  border: 1.3px solid black;
  border-radius: 20px;
  padding: 5px 10px;
  float: right;
}
.onlineList button:hover {
  border: 1.3px solid honeydew;
  color: honeydew;
  transition: 0.2s;
}
@-webkit-keyframes fadeInDown {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(0);
            transform: translate(-50%, -50%) scale(0);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
  }
}
@keyframes fadeInDown {
  0% {
    -webkit-transform: translate(-50%, -50%) scale(0);
            transform: translate(-50%, -50%) scale(0);
  }
  100% {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
  }
}
/*# sourceMappingURL=index.css.map */
.video {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}
.video video {
  margin-bottom: 20px;
  max-width: 100%;
  background: black;
  width: 300px;
}
.video button {
  margin-bottom: 20px;
  border: 1px solid #2a2b37;
  padding: 5px 10px;
  border-radius: 20px;
}
.video i {
  font-size: 48px;
  margin-bottom: 20px;
  -webkit-animation: alwaysRotate;
          animation: alwaysRotate;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
.video .video-stream {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.video .streamer-name {
  margin: auto;
  font-size: 18px;
  font-weight: 600;
  color: #2a2b37;
}
@-webkit-keyframes alwaysRotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes alwaysRotate {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
